import { createGlobalStyle } from "styled-components"

export const GlobalStyles = createGlobalStyle`
  :root {
    --main-color: "rgb(246, 247, 248)";
    --secondary-color: "#2C2C2C";
    --font-color: "#000000";
    --secondary-font-color: "#DFDFDF";
    --box-color: "#fff";
    --box-border: "#b7b7b7";
    --box-shadow: "rgba(233,240,243,0.5)";
    --box-shadow2: "#E6ECF8";
    --button-color: "#8d8d8d";
    --button-border: "#8d8d8d";
    --button-font: "#fff";
    --footer-label: "#909090";
  } 
  
  html.dark {
    --main-color: "#0a192f";
    --secondary-color: "#0a192f";
    --font-color: "#ccd6f6";
    --secondary-font-color: "#ccd6f6";
    --box-color: "#172a45";
    --box-border: "#2a3554";
    --box-shadow: "#0f0f29";
    --box-shadow2: "#1b1b3e";
    --button-color: "#172a45";
    --button-border: "#64ffda";
    --button-font: "#64ffda";
    --footer-label: "#8892b0";
  }
  
  body {
    background: var(--main-color);
    color: var(--font-color);
  }
`